import PropTypes from "prop-types";
import { useState } from "react";
// @mui
import { useTheme } from "@mui/material/styles";
import { Checkbox, TableRow, TableCell, MenuItem } from "@mui/material";
// utils
import { fDateTime } from "../../../../utils/formatTime";
import { fCurrency } from "../../../../utils/formatNumber";
// components
import Iconify from "../../../../components/Iconify";
import Label from "../../../../components/Label";
import { TableMoreMenu } from "../../../../components/table";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

TicketTableRow.propTypes = {
  row: PropTypes.object.isRequired,
  selected: PropTypes.bool,
  onSelectRow: PropTypes.func,
  onViewRow: PropTypes.func,
  onViewDetail: PropTypes.func,
};

export default function TicketTableRow({
  row,
  selected,
  onSelectRow,
  onViewRow,
  onViewDetail,
}) {
  const theme = useTheme();

  const [openMenu, setOpenMenuActions] = useState(null);

  const handleOpenMenu = (event) => {
    setOpenMenuActions(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpenMenuActions(null);
  };

  const { t } = useTranslation();

  const STATUS_CONFIG = {
    // SUCCESS
    COMPLETE: {
      level: "success",
      icon: "eva:checkmark-circle-fill",
    },
    F: {
      level: "success",
      icon: "mdi:check-decagram",
    },
    PAID: {
      level: "success",
      icon: "eva:checkmark-circle-fill",
    },

    // WARNING
    PENDING: {
      level: "warning",
      icon: "eva:clock-outline",
    },
    P: {
      level: "warning",
      icon: "eva:clock-outline",
    },
    N: {
      level: "warning",
      icon: "eva:clock-outline",
    },
    R: {
      level: "warning",
      icon: "eva:clock-outline",
    },
    PENDING_ACTIVATION: {
      level: "warning",
      icon: "eva:clock-outline",
    },

    // ERROR
    FAILED: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
    I: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
    A: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
    CANCELED: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
    INCIDENCE: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
    REJECTED: {
      level: "error",
      icon: "eva:close-circle-fill",
    },
  };

  const DEFAULT_ICON = "eva:minus-circle-outline";
  const DEFAULT_LEVEL = "default";

  const cfg = STATUS_CONFIG[row.transaction_status] || {};
  const level = cfg.level || DEFAULT_LEVEL;
  const icon = cfg.icon || DEFAULT_ICON;

  const iconColor =
    level === "success"
      ? "success.main"
      : level === "warning"
      ? "warning.main"
      : level === "error"
      ? "error.main"
      : "grey.500";

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>
      <TableCell padding="checkbox" />
      <TableCell align="center">{row?.ref_code}</TableCell>
      <TableCell align="center">{row?.transaction_id}</TableCell>

      <TableCell align="left">{row?.full_name || "-"}</TableCell>
      <TableCell align="center">{fDateTime(row?.created_on)}</TableCell>
      <TableCell align="center">{row?.operator_name}</TableCell>
      <TableCell align="center">{row?.phone || "-"}</TableCell>
      <TableCell align="center">{fCurrency(row?.amount)}</TableCell>
      <TableCell align="center">{fCurrency(row?.percent)}</TableCell>
      <TableCell align="center">{fCurrency(row?.sp_percent)}</TableCell>

      <TableCell align="center">
        <Iconify
          icon={icon}
          sx={{
            width: 20,
            height: 20,
            color: iconColor,
          }}
        />

        <Label
          variant={theme.palette.mode === "light" ? "ghost" : "filled"}
          color={level}
          sx={{ textTransform: "capitalize" }}
        >
          {t(`remesas.status_transac.${row.transaction_status}`)}
        </Label>
      </TableCell>

      <TableCell align="right">
        <TableMoreMenu
          open={openMenu}
          onOpen={handleOpenMenu}
          onClose={handleCloseMenu}
          actions={
            <>
              <MenuItem
                onClick={() => {
                  onViewRow();
                  handleCloseMenu();
                }}
              >
                <Iconify icon={"eva:eye-fill"} />
                {t("field.view")}
              </MenuItem>
              {row?.is_remesas && (
                <MenuItem
                  onClick={() => {
                    onViewDetail();
                    handleCloseMenu();
                  }}
                >
                  <Iconify icon={"ooui:view-details-ltr"} />
                  {t("field.detail_transaction")}
                </MenuItem>
              )}
            </>
          }
        />
      </TableCell>
    </TableRow>
  );
}
